<template>
<div class="main"
      :style="{'background-color':config.Backgrounds,
              'padding':`${config.TBPadding/50}rem ${config.LRPadding/50}rem`}">
  <div class="category-area-container"
        :style="{'background-color':config.CategoryBackgroundColor,
        'border-radius':`${(Number(config.BackgroundFilletBackgroundFillet)/50)}rem`}"
        :class="{'wrap': config.settingsType === '0','over-flow-scroll': config.settingsType === '2' }">
        <template v-for="(item, idx) in config.AddCategory">
          <!-- 'width':`${1.24 - (((config.LRPadding/50)*2)/5)}rem`, -->
          <div class="item"
                :style="{
                'width':`${1.24 - (((config.LRPadding/50)*2)/5)}rem`,
                'margin-left': config.settingsType === '2' && idx === 5? '0.26rem':'',
                'color': config.TextColor}"
                :key="item.id"
                :data-mIndex="index"
                :data-index="idx"
                :data-remark="item.Remarks"
                :data-url="item.shareLine"
                :data-type="config.modelCode"
                v-if="(item.iconName && !lock) || lock"
                @click="handleClick(item,idx)">
            <div class="cate-icon">
              <img :src="item.img.length>0?item.img[0].url : item.defaultImg" :style="{'border-radius': (Number(config.ImageRadius)/50)+'rem'}">
            </div>
            <span class="cate-text">{{item.iconName}}</span>
          </div>
        </template>
  </div>
</div>
</template>
<script>
import mixins from './common-mixins'
import linkUtil from './utils/linkUtil'

/**
 * @description 频道页 品类区组件
*/
export default {
  mixins: [mixins],
  props: {
    // /**
    //  * @description 类型： 0: 固定两行 1: 固定一行 2: 一行滑动
    //  * 固定2行：需配置2行总共10个图标，上传数量6到10个
    //  * 固定1行：需配置1行总共5个图标，上传数量2到5个
    //  * 1行滑动：配置1行最多10个图标，支持左右滑动，同时可配置滑动标的颜色，上传数量2-10个
    //  */
    // type: {
    //   type: Number,
    //   default: function () {
    //     return 0
    //   },
    // },
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {

    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 品类点击
    */
    handleClick(item, idx) {
      if (this.lock) return
      let param = {
        site_number: idx + 1 + '',
        remark: item.Remarks || 'null',
        goto_url: item.shareLine?.trim() || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      if (item.shareLine?.trim()) {
        this.$emit('handleClick')
      }
      linkUtil.handleLinkClick({
        linkType: item.shareTypes,
        link: item.shareLine,
        showBar: item.shareLineRadios,
        needLogin: item.shareLogin
      })
      // if (!item.shareLine || item.shareLine.indexOf('http') === -1) return

      // let targetUrl = item.shareLine
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!item.shareLineRadios}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!item.shareLineRadios}`
      // }
      // if (item.shareLogin) {
      //   if (this.$store.state.isLogin) { // 自定义链接无导航
      //     window.location.href = targetUrl
      //   } else {
      //     this.$store.dispatch('doLogin')
      //   }
      // } else {
      //   window.location.href = targetUrl
      // }
    }
  },
}
</script>

