<template>
  <div class="multi-img-row-container" :style="{'padding-left': (config.LRPadding/50)+ 'rem',
                                                    'padding-right': (config.LRPadding/50)+ 'rem',
                                                    'padding-top': (config.TBPadding/50)+ 'rem',
                                                    'padding-bottom': (config.TBPadding/50)+ 'rem',
                                                    'background-color':config.Backgrounds}">
    <div class="row">
      <div class="item"
            v-for="(item, idx) in (config.AddRowTowImage || config.AddRowThreeImage || config.AddRowFourImage || [])"
            :key="item.id"
            :data-mIndex="index"
            :data-index="idx"
            :data-remark="item.Remarks"
            :data-url="item.shareLine"
            :data-type="config.modelCode"
            :style="{'padding':(config.ImgPadding/50)+ 'rem'}"
            @click="handleClick(item,idx)">
        <img :src="item.img.length>0?item.img[0].url : item.defaultImg" :style="{'border-radius': (Number(config.ImageRadius)/50)+ 'rem'}">
      </div>
    </div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

/**
 * @description 频道页 一行多张图片组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {
      imgList: []
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 图片点击
    */
    handleClick(item, idx) {
      if (this.lock) return
      let param = {
        site_number: idx + 1 + '',
        remark: item.Remarks || 'null',
        goto_url: item.shareLine?.trim() || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      if (item.shareLine?.trim()) {
        this.$emit('handleClick')
      }
      linkUtil.handleLinkClick({
        linkType: item.shareTypes,
        link: item.shareLine,
        showBar: item.shareLineRadios,
        needLogin: item.shareLogin
      })
      // if (!item.shareLine || item.shareLine.indexOf('http') === -1) return

      // let targetUrl = item.shareLine
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!item.shareLineRadios}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!item.shareLineRadios}`
      // }
      // if (item.shareLogin) {
      //   if (this.$store.state.isLogin) { // 自定义链接无导航
      //     window.location.href = targetUrl
      //   } else {
      //     this.$store.dispatch('doLogin')
      //   }
      // } else {
      //   window.location.href = targetUrl
      // }
    }
  },
}
</script>

