<template>
  <div class="swiper-container" v-if="configFilter.length>0" ref="swiper" :style="{'padding-left': (config.LRPadding/50)+ 'rem','padding-right': (config.LRPadding/50)+ 'rem','padding-top': (config.TBPadding/50)+ 'rem','padding-bottom': (config.TBPadding/50)+ 'rem'}">
      <van-swipe ref="vanSwiper"
      :height="config.ModuleHeight"
      :autoplay="3000"
      :duration="500"
      indicator-color="white"
      class="banner"
      :style="{'border-radius':(Number(config.ImageRadius)/50) + 'rem'}" @change="swiperChange">
        <van-swipe-item
          class="item"
          v-for="(item, idx) in configFilter"
          :data-mIndex="index"
          :data-index="idx"
          :data-remark="item.Remarks"
          :data-url="item.shareLine"
          :data-type="config.modelCode"
          :key="idx">
          <img :src="item.img.length>0?item.img[0].url : item.defaultImg" :style="{'background-color':config.Backgrounds, 'border-radius':(Number(config.ImageRadius)/50) + 'rem'}" @click="bannerClick(item, idx)"/>
        </van-swipe-item>
      </van-swipe>
      <div class="background-bg" :style="{'background-color':config.Backgrounds,'opacity': config.BackgroundOpacity}"></div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

/**
 * @description 频道页 轮播组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置队形
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {
    }
  },
  created() {
  },
  watch: {
    config: {
      deep: true,
      handler(newValue, oldValue) {
        if (newValue !== oldValue) {
          if (this.lock && newValue.LRPadding !== oldValue.LRPadding) {
            let swiperItem = this.$refs.swiper.querySelectorAll(`.van-swipe-item`)
            console.log(swiperItem)
            if (swiperItem?.length) {
              this.$nextTick(() => {
                swiperItem.forEach(i => {
                  i.style.width = (375 - 2 * this.config?.LRPadding) + 'px'
                })
              })
            }
          }
        }
      }
    }
  },
  computed: {
    /**
     * @description 元数据过滤生成有效数据列表
    */
    configFilter() {
      let ret = []
      if (this.lock) {
        ret = this.config.AddRotationChart
      } else {
        let now = new Date()
        ret = this.config.AddRotationChart.filter(i => {
          let startTime = new Date(i.starttime.replace(/-/g, '/'))
          let endTime = new Date(i.endtime.replace(/-/g, '/'))
          console.log(i.starttime.replace(/-/g, '/'))
          if (i.starttime && i.endtime) {
            return now >= startTime && now <= endTime
          } else if (i.starttime && !i.endtime) {
            return now >= startTime
          } else if (!i.starttime && i.endtime) {
            return now <= endTime
          }
        })
        // return this.config.AddRotationChart.filter(i => {
        //   let starttime = new Date(i.starttime)
        //   let endtime = new Date(i.endtime)
        //   return now >= starttime && now <= endtime
        // })
      }
      return ret
    }
  },
  mounted() {
    this.swipeResize(this, 'vanSwiper')
    console.log('config:')
    console.log(this.config)
  },
  methods: {
    /**
     * @description 轮播图滑动
    */
    swiperChange(index) {
    },
    /**
     * banner 点击
    */
    bannerClick(item, idx) {
      if (this.lock) return
      let param = {
        site_number: idx + 1 + '',
        remark: item.Remarks || 'null',
        goto_url: item.shareLine?.trim() || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      if (item.shareLine?.trim()) {
        this.$emit('handleClick')
      }
      linkUtil.handleLinkClick({
        linkType: item.shareTypes,
        link: item.shareLine,
        showBar: item.shareLineRadios,
        needLogin: item.shareLogin
      })
      // if (!item.shareLine || item.shareLine.indexOf('http') === -1) return

      // let targetUrl = item.shareLine
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!item.shareLineRadios}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!item.shareLineRadios}`
      // }
      // if (item.shareLogin) {
      //   if (this.$store.state.isLogin) { // 自定义链接无导航
      //     window.location.href = targetUrl
      //   } else {
      //     this.$store.dispatch('doLogin')
      //   }
      // } else {
      //   window.location.href = targetUrl
      // }
    },
    /**
   * 解决窗口大小发生变化时，vant swipe尺寸不更新问题，建议所有使用vant swipe组件的页面添加该方法
   * @param {*} vue Vue实例
   * @param {*} swiper  swipe对应的ref name
   */
    swipeResize(vue, swiper) {
      const _swiper = swiper;
      const _vue = vue;
      (function() {
        window.addEventListener('resize', resizeThrottler, false);

        var resizeTimeout;
        function resizeThrottler() {
          if (!resizeTimeout) {
            resizeTimeout = setTimeout(function() {
              resizeTimeout = null;
              actualResizeHandler();
            }, 1000);
          }
        }
        function actualResizeHandler() {
          if (_vue && _vue.$refs && _vue.$refs[_swiper]) {
            _vue.$refs[_swiper].resize()
          }
        }
      }());
    }
  },
}
</script>

<style lang="scss" scoped>
.swiper-container{
  padding: .24rem;
  position: relative;
  .background-bg{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
  }
  .banner{
    transform: translateZ(0);
     overflow: hidden;
     z-index: 2;
  // border-radius: .24rem;
  img{
    width: 100%;
    height: auto;
    transform: translateZ(0);
    background-color: #ffffff;
  }
}

}
</style>
