<template>
  <div class="product-single-container"
  v-if="config.AddBigPicture && config.AddBigPicture.length>0"
  :style="{'background':config.Backgrounds,'padding-top': (config.TBPadding/50)+ 'rem',
        'padding-bottom': (config.TBPadding/50)+ 'rem',
        'padding-left': (config.LRPadding/50)+ 'rem',
        'padding-right': (config.LRPadding/50)+ 'rem'}">
    <div class="item"
        :style="{
        'border-radius': (Number(config.ImageRadius)/50)+ 'rem',
        'margin':(config.ImgPadding/50)+ 'rem'
        }"
        v-for="(item, idx) in config.AddBigPicture"
        :data-mIndex="index"
        :data-index="idx"
        :data-remark="item.Remarks"
        :data-url="linkUrl(item)"
        :data-type="config.modelCode"
        @click="toGoodInfo(item, idx)"
        :key="item.id">
      <div class="img-area"
      :style="{'border-radius': (Number(config.ImageRadius)/50)+ 'rem', 'height': imgHeight}">
        <img :src="item.img.length>0?item.img[0].url : item.defaultImg">
      </div>
      <div class="info-area">
        <div class="info-left">
          <div class="pro-title"
          v-if="config.showContents[0].checked"
          :style="{'color':config.showContents[0].colors,
                    '-webkit-line-clamp':config.showContents[0].radios?'2':'1',}">
            {{item.customTitle || (item.img.length>0?item.img[0].name:'')}}
          </div>
          <div class="selling-point"
                v-if="config.showContents[1].checked && item.img.length && item.img[0].sellingPoint && item.img[0].sellingPoint.length > 0">
            <span class="selling-point-item"
            :style="{'color':config.showContents[1].colors}"
            v-for="(point,index) in item.img[0].sellingPoint"
            :key="'single'+index">
            <span class="dot">·</span>{{point}}
            </span>
          </div>
          <div class="price-area"
          :style="{'color':config.showContents[2].colors}"
                v-if="config.showContents[2].checked && item.img.length">
            <div class="price">
              <span class="yuan">¥</span>
              <span class="price-number-big">{{item.img[0].price | formatPrice | formatPriceInt}}</span>
              <span class="price-number-small">{{item.img[0].price | formatPrice | formatPriceFloat}}</span>
            </div>
            <!-- <div class="price-tag">
              <span class="tag-activity">活动价</span>
            </div> -->
          </div>
          <!-- <div class="tag-area"></div>
          <div class="tips-area">本店买过的商品</div> -->
        </div>
        <!-- <div class="info-right">
          <span class="activity-tip">活动日期：11.01-11.11</span>
          <span class="comment-info">8条评价 98%好评</span>
        </div> -->
      </div>
      <div class="activity-icon" v-if="config.showContents[3].checked && config.showContents[3].angleMarkUlr.length">
        <img :src="config.showContents[3].angleMarkUlr[0].url">
      </div>
    </div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

// import axios from 'axios'
/**
 * @description 频道页 商品大图组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  // watch: {
  //   config: {
  //     immediate: true,
  //     deep: true,
  //     handler(newValue, oldValue) {
  //       // debugger
  //       if (newValue !== oldValue) {
  //         // 如果是选择分类，需要调用接口获取商品列表
  //         if (newValue.shopRadios && newValue.stopCate && newValue.stopCate.length > 0) {
  //           this.initGoodData()
  //         } else {
  //           this.goodConfigList = newValue.AddBigPicture || []
  //         }
  //       }
  //     }
  //   }
  // },
  filters: {
    formatPrice(value) {
      if ((value || value === 0) && value !== 'NaN') {
        return parseFloat(value).toFixed(2)
      } else {
        return '0'
      }
    },
    formatPriceInt(v) {
      return v.split('.')[0];
    },
    formatPriceFloat(v) {
      return '.' + v.split('.')[1];
    },
  },
  data() {
    return {
      goodConfigList: [], // 商品列表
    }
  },
  computed: {
    imgHeight() {
      // (config.LRPadding/50)
      // (config.ImgPadding/50)+ 'rem'
      // calc(100vw - ${(config.LRPadding/50)*2}rem - ${(config.ImgPadding/50)*2}rem)
      let pagePadding = (this.config.LRPadding / 50) * 2 + 'rem'
      let imgPadding = (this.config.ImgPadding / 50) * 2 + 'rem'
      let contentWidth = this.lock ? '375px' : '100vw'
      return `calc((${contentWidth} - ${pagePadding} - ${imgPadding}))`
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    linkUrl(item) {
      let ret = ''
      if (item.img.length) {
        ret = linkUtil.isScMp() ? '/pages/product/detail/index?productId=' + item.img[0].uid : 'https://m-test.ehaier.com/sgmobile/goodsDetail?productId=' + item.img[0].uid
      }
      return ret
    },
    /**
     * @description 选择分类时，加载分类下商品列表
    */
    initGoodData() {
      // let url = process.env.VUE_APP_ENV === 'development'?''
      // axios
      //   .get(url, {
      //     params: param,
      //     // paramsSerializer: Qs.stringify(param, { arrayFormat: 'repeat' }),
      //     ...config,
      //   })
      //   .then((res) => {
      //     resolve(res.data)
      //   })
    },
    /**
     * @description 跳转单品页
    */
    toGoodInfo(item, idx) {
      if (this.lock) return
      if (!item.img || item.img.length === 0) return
      let param = {
        site_number: idx + 1 + '',
        remark: item.Remarks || 'null',
        goto_url: this.linkUrl(item) || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      let queryParam = {
        productId: item.img[0].uid,
      }
      this.$emit('handleClick')
      if (linkUtil.isScMp()) {
        // 小程序环境跳转小程序搜索页
        linkUtil.openLink('/pages/product/detail/index', queryParam)
      } else {
        // 非小程序页面 m站跳转
        this.$router.push({
          path: 'goodsDetail',
          query: queryParam,
        })
      }
    }
  },
}
</script>

