<template>
  <div class="column-split-container" :style="{'padding-left': (config.LRPadding/50)+ 'rem',
                                                'padding-right': (config.LRPadding/50)+ 'rem',
                                                'padding-top': (config.TBPadding/50)+ 'rem',
                                                'padding-bottom': (config.TBPadding/50)+ 'rem',
                                                'background-color':config.Backgrounds}">
    <div class="left item"
        :data-mIndex="index"
        :data-index="0"
        :data-remark="config.AddOneLeft[0].Remarks"
        :data-url="config.AddOneLeft[0].shareLine"
        :data-type="config.modelCode"
        @click="handleClick(config.AddOneLeft[0],0)">
        <div :style="{'padding':(config.ImgPadding/50)+ 'rem'}">
          <img :src="config.AddOneLeft[0].img.length>0?config.AddOneLeft[0].img[0].url:config.AddOneLeft[0].defaultImg"
                :style="{'border-radius':(Number(config.ImageRadius)/50)+ 'rem'}">
        </div>
    </div>
    <div class="right">
      <div class="right-top sub item"
          @click="handleClick(config.AddOneLeft[1],1)"
            :data-mIndex="index"
            :data-index="1"
            :data-remark="config.AddOneLeft[1].Remarks"
            :data-url="config.AddOneLeft[1].shareLine"
            :data-type="config.modelCode"
          :style="{'padding':(config.ImgPadding/50)+ 'rem'}">
        <img :src="config.AddOneLeft[1].img.length>0?config.AddOneLeft[1].img[0].url:config.AddOneLeft[1].defaultImg"
              :style="{'border-radius':(Number(config.ImageRadius)/50)+ 'rem'}">
      </div>
      <div class="right-bottom sub item"
            @click="handleClick(config.AddOneLeft[2],2)"
            :data-mIndex="index"
            :data-index="2"
            :data-remark="config.AddOneLeft[2].Remarks"
            :data-url="config.AddOneLeft[2].shareLine"
            :data-type="config.modelCode"
            :style="{'padding':(config.ImgPadding/50)+ 'rem'}">
        <img :src="config.AddOneLeft[2].img.length>0?config.AddOneLeft[2].img[0].url:config.AddOneLeft[2].defaultImg"
              :style="{'border-radius':(Number(config.ImageRadius)/50)+ 'rem'}">
      </div>
    </div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

/**
 * @description 频道页 一左二右图片组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 图片点击
    */
    handleClick(item, idx) {
      if (this.lock) return
      let param = {
        site_number: idx + 1 + '',
        remark: item.Remarks || 'null',
        goto_url: item.shareLine?.trim() || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      if (item.shareLine?.trim()) {
        this.$emit('handleClick')
      }
      linkUtil.handleLinkClick({
        linkType: item.shareTypes,
        link: item.shareLine,
        showBar: item.shareLineRadios,
        needLogin: item.shareLogin
      })
      // if (!item.shareLine || item.shareLine.indexOf('http') === -1) return

      // let targetUrl = item.shareLine
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!item.shareLineRadios}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!item.shareLineRadios}`
      // }
      // if (item.shareLogin) {
      //   if (this.$store.state.isLogin) { // 自定义链接无导航
      //     window.location.href = targetUrl
      //   } else {
      //     this.$store.dispatch('doLogin')
      //   }
      // } else {
      //   window.location.href = targetUrl
      // }
    }
  },
}
</script>

