<template>
  <div class="search-container item"
    :data-mIndex="index"
    :data-index="0"
    :data-remark="config.Remarks"
    :data-url="linkUrl"
    :data-type="config.modelCode"
    :style="{'background-color':config.Backgrounds,
  'padding':`${config.TBPadding/50}rem ${config.LRPadding/50}rem`}">
    <div class="search-content"
    :style="{'border-color': config.BorderColor,
    'background-color':config.SearchBackground,
    'color':config.TextColor,
    'height':(config.FrameHeight/50)+ 'rem',
    'border-radius': (Number(config.ImageRadius)/50)+ 'rem'}"
     @click="toSearch">
      <div class="search-icon"></div>
      <span class="search-placeholder" :style="{'text-align': config.TextAlign}">{{config.PresetText|| '请输入搜索内容'}}</span>
    </div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

/**
 * @description 频道页 搜索框组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {
    }
  },
  computed: {
    linkUrl() {
      return linkUtil.isScMp() ? linkUtil.isSyBirdMP() ? '/subPages/homeSearch/index' : '/pages/search/init/index' : 'https://m-test.ehaier.com/sgmobile/goodsSearch'
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 跳转搜索
    */
    toSearch() {
      if (this.lock) return
      let param = {
        site_number: '1',
        remark: this.config?.Remarks || 'null',
        goto_url: linkUtil.isScMp() ? linkUtil.isSyBirdMP() ? '/subPages/homeSearch/index' : '/pages/search/init/index' : 'https://m-test.ehaier.com/sgmobile/goodsSearch',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      this.$emit('handleClick')
      if (linkUtil.isScMp()) {
        // 小程序环境跳转小程序搜索页
        let skipUrl = '/pages/search/init/index'
        if (linkUtil.isSyBirdMP()) {
          // 三翼鸟小程序路径替换
          skipUrl = '/subPages/homeSearch/index'
        }
        linkUtil.openLink(skipUrl)
      } else {
        // 非小程序页面 m站跳转
        // linkUtil.openLink('/goodsSearch')
        this.$router.push({
          path: '/goodsSearch',
        })
      }
    }
  },
}
</script>

