    // margin-top: .24rem;
    // margin-bottom: .24rem;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: #999;
    .search-icon{
        width: .44rem;
        height: .44rem;
        margin-right: .14rem;
        vertical-align: middle;
        margin-left: .2rem;
        background: url('https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/home/new/search.png') center center no-repeat;
        background-size: cover;
    }
    .search-placeholder{
      font-size: .24rem;
