<template>
  <div class="auxiliary-blank-container item"
      :data-mIndex="index"
      :data-index="0"
      :data-remark="config.Remarks"
      :data-url="''"
      :data-type="config.modelCode"
      @click="handleClick"
      :style="{'background-color':config.Backgrounds,'height': (config.FrameHeight/50)+'rem'}">
  </div>
</template>
<script>
import mixins from './common-mixins'

/**
 * @description 频道页 辅助空白组件
*/
export default {
  mixins: [mixins],
  props: {
    /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 埋点参数
     */
    gioParams: {
      type: Object,
      default: function () {
        return {}
      },
    },
  },
  methods: {
    /**
     * @description 判断当前环境
    */
    currentPlatform() {
      let ret = 'M站'
      if (this.$util.isSyBird()) {
        ret = '三翼鸟APP'
      } else if (this.$util.isSyBirdMP()) {
        ret = '三翼鸟小程序'
      } else if (this.$util.isScMpWechat()) {
        ret = '商城微信小程序'
      } else if (this.$util.isScMpPeWechat()) {
        ret = '商城私域微信小程序'
      } else if (this.$util.isUplus()) {
        ret = '智家APP'
      } else if (this.$util.isScMpByte()) {
        ret = '商城抖音私域小程序'
      } else if (this.$util.isScMpAli()) {
        ret = '支付宝小程序'
      }
      return ret
    },
    handleClick() {
      if (this.lock) return
      let param = {
        site_number: '1',
        remark: this.config.Remarks || 'null',
        goto_url: 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
    }
  }
}
</script>
<style lang="scss" scoped>
.auxiliary-blank-container{
  background: #ffffff;
  width: 200%;
  height: .24rem;
  max-height: 2rem;
}
</style>
