<template>
  <div class="title-link-container item"
      :data-mIndex="index"
      :data-index="0"
      :data-remark="config.Remarks"
      :data-url="config.shareLine"
      :data-type="config.modelCode"
      :style="{'background-color':config.Backgrounds,
      'margin': `${config.TBPadding/50}rem  ${config.LRPadding/50}rem`}">
    <div class="left">{{config.TitleName}}</div>
    <div class="right" @click="handleClick">
      <span class="left-text">{{config.ForwardName}}</span>
      <span class="arrow"></span>
    </div>
  </div>
</template>
<script>
import linkUtil from './utils/linkUtil'
import mixins from './common-mixins'

/**
 * @description 频道页 标题链接组件
*/
export default {
  mixins: [mixins],
  props: {
  /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    }
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    /**
     * @description 点击链接
    */
    handleClick() {
      if (this.lock) return
      let param = {
        site_number: '1',
        remark: this.config?.Remarks || 'null',
        goto_url: this.config?.shareLine?.trim() || 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
      if (this.config.shareLine?.trim()) {
        this.$emit('handleClick')
      }
      linkUtil.handleLinkClick({
        linkType: this.config.LinkTo,
        link: this.config.shareLine,
        showBar: this.config.shareLineRadios,
        needLogin: this.config.shareLogin
      })
      // if (!this.config.LinkTo || this.config.LinkTo.indexOf('http') === -1) return

      // let targetUrl = this.config.LinkTo
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!this.config.shareLineRadios}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!this.config.shareLineRadios}`
      // }
      // if (this.config.shareLogin) {
      //   if (this.$store.state.isLogin) { // 自定义链接无导航
      //     window.location.href = targetUrl
      //   } else {
      //     this.$store.dispatch('doLogin')
      //   }
      // } else {
      //   window.location.href = targetUrl
      // }
    },
  }
}
</script>

