<template>
  <div class="channel" id="channel" ref="toTop">
    <skeleton v-if="skeletonLoading || !finished" :nums="3"></skeleton>
    <div v-else>
      <Header :leftClick="back" v-if="!hideHeader">
        <!-- <div v-if="showShare"  class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div> -->
        <div v-if="showShare" class="share-btn" slot="right" :style="{backgroundImage: `url(${$globalIcon.blackShareIcon})`}" @click="doShare"></div>
      </Header>
      <div class="no-data" v-if="noData">
        <span :class="{'no-data-icon': releaseStatus ===  -1,'not-commit-icon': releaseStatus ===  0}"></span>
        <span class="no-data-text">
          {{releaseStatus === 0 ? '当前页面未发布，请发布后再访问':'当前界面走丢啦，请返回重新查找吧~'}}
          </span>
      </div>
      <div v-else class="channel-content" :class="{'no-top-padding':$util.isScMp() || $util.isSyBirdMP()}" :style="{'margin-top': hideHeader?'0':''}" ref="channelContent">
        <template v-for="(item, idx) in dataConfig">
          <!-- 轮播组件 -->
          <swiper v-if="item.modelCode === 'rotationChart'" :index="idx" :config="item" :key="item.id" :gioParams="gioParams" @handleClick="handleClick"></swiper>
          <!-- 一行一张组件 -->
          <single-img-column v-if="item.modelCode === 'rowOneImage'" :index="idx" :config="item" :key="item.id" :gioParams="gioParams" @handleClick="handleClick"></single-img-column>
          <!-- 一行多张图片组件 -->
          <multi-img-row v-if="item.modelCode === 'rowTowImage' || item.modelCode === 'rowThreeImage' || item.modelCode === 'rowFourImage'"
                          :config="item"
                          :index="idx"
                          :gioParams="gioParams"
                          :key="item.id" @handleClick="handleClick">
          </multi-img-row>
          <!-- 一左二右图片组件 -->
          <column-split v-if="item.modelCode === 'oneLeft'" :index="idx" :gioParams="gioParams" :config="item" :key="item.id" @handleClick="handleClick"></column-split>
          <!-- 一上二下图片组件 -->
          <row-split v-if="item.modelCode === 'oneTop'" :config="item" :gioParams="gioParams" :index="idx" :key="item.id" @handleClick="handleClick"></row-split>
          <!-- 商品大图组件 -->
          <product-single v-if="item.modelCode === 'bigPicture'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></product-single>
           <!-- 一行两个商品组件 -->
          <product-row-two v-if="item.modelCode === 'bigRowTwoImage'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></product-row-two>
          <!-- 一行三个商品组件 -->
          <product-row-three v-if="item.modelCode === 'bigRowThreeImage'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></product-row-three>
          <!-- 辅助空白组件 -->
          <auxiliary-blank v-if="item.modelCode === 'auxiliary'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id"></auxiliary-blank>
          <!-- 标题链接 -->
          <title-link v-if="item.modelCode === 'titleLinkArea'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></title-link>
          <!-- 富文本组件 -->
          <rich-text v-if="item.modelCode === 'richTextArea'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id"></rich-text>
          <!-- 品类区组件 -->
          <category-area v-if="item.modelCode === 'category'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></category-area>
          <!-- 搜索组件 -->
          <search v-if="item.modelCode === 'searchArea'" :config="item" :index="idx" :gioParams="gioParams" :key="item.id" @handleClick="handleClick"></search>
        </template>
      </div>
    </div>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>
<script>
import skeleton from '@/components/skeleton'
import Header from '@/components/header'
import { mapGetters } from 'vuex'
import mixins from '@/components/top-feedBack/mixins.js'
import UplusApi from '@uplus/uplus-api';

import swiper from '@/web-modules/swiper'
import singleImgColumn from '@/web-modules/single-img-column'
import multiImgRow from '@/web-modules/multi-img-row'
import columnSplit from '@/web-modules/column-split'
import rowSplit from '@/web-modules/row-split'
import categoryArea from '@/web-modules/category-area'
import auxiliaryBlank from '@/web-modules/auxiliary-blank'
import productRowTwo from '@/web-modules/product-row-two'
import productRowThree from '@/web-modules/product-row-three'
import titleLink from '@/web-modules/title-link'
import productSingle from '@/web-modules/product-single'
import search from '@/web-modules/search'
import richText from '@/web-modules/rich-text'
// import customImg from './components/custom-img'
import personalApi from '@/http/service/index'
// import utils from '@/utils/utils'
// import Vue from 'vue'
// import { wdCommonSearchNew } from '@/http/service/search'
// import { getPriceByProductList } from '@/http/service/shop'
const instance = new UplusApi()
export default {
  mixins: [mixins],
  components: {
    skeleton,
    Header,
    columnSplit, // 一左二右图片组件
    rowSplit, // 一上二下图片组件
    // customImg, // 自定义图片组件
    multiImgRow, // 一行多张图片组件
    search, // 搜索组件
    productSingle, // 商品大图组件
    richText, // 富文本组件
    productRowTwo, // 一行多个商品组件
    productRowThree,
    swiper, // 轮播组件
    singleImgColumn, // 一行一张图片组件
    titleLink, // 标题链接
    categoryArea, // 品类区组件
    auxiliaryBlank, // 辅助空白
  },
  data() {
    return {
      skeletonLoading: true,
      pageConfigList: [], // 频道配置列表
      channelId: '', // 频道页id
      resultData: null,
      dataConfig: [], // 组件配置列表
      showShare: this.$util.isHaierApp(),
      hideHeader: false, // 是否隐藏头部
      noData: false, // 缺省
      releaseStatus: -1, // 发布状态 0未发布，1已发布，-1 id不存在
      productActList: [], // 实际商品信息列表
      finished: false,
      startTime: 0, // 进入页面的时间戳
      gioReportedList: [], // 已曝光埋点列表（埋点只曝光一次）
      IntersectionObserver: null
    }
  },
  created() {
    this.gioReportedList = []
    // this.channelId = this.$route.query.id;
    // this.hideHeader = this.$route.query.hideHeader;
    // this.initData()
  },
  activated() {
    console.log('activated==============> in')
    this.startTime = new Date().getTime()
    this.channelId = this.$route.query.id;
    this.hideHeader = this.$route.query.hideHeader;
    this.gioReportedList = []
    if (this.resultData) {
      console.log('activated =======> 0')
      // 频道页浏览埋点
      let param = {
        page_id: this.channelId + '', // 页面id
        title: this.resultData?.pageName || 'null', // 页面标题
        url: document.URL + '', // 页面URL
        ex_page_url: document.referrer || 'null', // 上级页面URL/路径
        custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
      }
      this.$util.gioTrackWithVariable('mall_channel_view', param)
      window.setTimeout(() => {
        this.createIntersectionObserver()
      }, 500)
    } else {
      console.log('activated =======> 1')

      this.initData()
    }
  },
  beforeRouteLeave (to, from, next) {
    // let endTime = new Date().getTime()
    // let param = {
    //   page_id: this.channelId ? this.channelId + '' : 'null', // 页面ID
    //   title: this.$route.meta.title, // 页面标题
    //   entry_time: this.startTime + '', // 进入页面时间
    //   leave_time: endTime + '', // 离开页面时间
    //   page_stay_time: (endTime - this.startTime) / 1000 + '', // 停留时长
    // }
    // this.$util.gioTrackWithVariable('mall_channel_stayTime', param) // 页面停留时间埋点
    // 离开页面前清除 IntersectionObserver
    if (this.IntersectionObserver) {
      this.IntersectionObserver.disconnect()
    }

    next();
  },
  beforeDestroy() {
    // 智家APP onresume生命周期检查支付状态,移除监听
    instance.removeResumeEventListener('onresume_key')
  },
  mounted() {
    console.log('mounted')
    // if (utils.isScMp() || utils.isSyBirdMP()) {
    //   console.log('小程序环境=========>')
    //   utils.loadScript('https://cdn09.ehaier.com/shunguang/H5/www/microMall/gio-wx.js').then(() => {
    //     console.log('loadScript gio-wx=========>', gio)
    //     gio('init', 'b20ca947ec6bd029', 'wx027e74467882ea1c', {
    //       // version: '1.0',
    //       // followShare: true,
    //       // vue: Vue
    //     });
    //     gio('send');
    //     let param = {
    //       page_id: '测试id', // 页面id
    //       title: '小程序埋点测试' || 'null', // 页面标题
    //       url: document.URL + '', // 页面URL
    //       ex_page_url: document.referrer || 'null', // 上级页面URL/路径
    //       custom_platform: '商城微信小程序' // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
    //     }
    //     console.log('mpGio', 'mall_channel_view', param)
    //     gio('track', 'mall_channel_view', param)
    //   })
    // }
    // 添加 智家app环境页面监听
    this.addListener()
  },
  computed: {
    ...mapGetters(['mId']),
    location() {
      return this.$store.getters['getlocationInfo']
    },
    getShareId() {
      return sessionStorage.getItem('shareUserId') || null;
    },
    memberId() {
      return sessionStorage.getItem('shareUserId') || '';
    },
    gioParams() {
      return {
        page_id: this.channelId + '', // 页面id
        title: this.resultData?.pageName || 'null', // 页面标题
        url: document.URL + '', // 页面URL
        custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
      }
    }
  },
  methods: {
    /**
     * @description 监听组件跳转
    */
    handleClick() {
      let endTime = new Date().getTime()
      let param = {
        page_id: this.channelId ? this.channelId + '' : 'null', // 页面ID
        title: this.$route.meta.title, // 页面标题
        entry_time: this.startTime + '', // 进入页面时间
        leave_time: endTime + '', // 离开页面时间
        page_stay_time: Math.floor((endTime - this.startTime) / 1000) + '', // 停留时长
        url: document.URL + '', // 页面URL
        custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
      }
      this.$util.gioTrackWithVariable('mall_channel_stayTime', param) // 页面停留时间埋点
    },
    initData() {
      console.log('initdata ===>')
      if (!this.channelId) return
      personalApi.detailInfo({ 'id': this.channelId }).then(res => {
        console.log('detailInfo ===>')

        // debugger
        if (res.success && res.result?.releaseStatus === 1) {
          this.resultData = res.result
          this.$route.meta.title = res.result?.pageName
          // 频道页浏览埋点
          let param = {
            page_id: this.channelId + '', // 页面id
            title: res.result?.pageName || 'null', // 页面标题
            url: document.URL + '', // 页面URL
            ex_page_url: document.referrer || 'null', // 上级页面URL/路径
            custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
          }
          this.$util.gioTrackWithVariable('mall_channel_view', param)
          let skuList = []
          try {
            this.dataConfig = JSON.parse(this.resultData.moduleAssemblyDetailList[0].configContent)

            // 过滤失效轮播
            // item.modelCode === 'rotationChart'
            this.dataConfig = this.dataConfig.filter(d => {
              let ret = true
              if (d.modelCode === 'rotationChart') {
                console.log('rotationChart filter =======>')
                let now = new Date()
                let expireSwiper = d.AddRotationChart.filter(i => {
                  let startTime = new Date(i.starttime.replace(/-/g, '/'))
                  let endTime = new Date(i.endtime.replace(/-/g, '/'))
                  console.log(i.starttime.replace(/-/g, '/'))
                  if (i.starttime && i.endtime) {
                    return now >= startTime && now <= endTime
                  } else if (i.starttime && !i.endtime) {
                    return now >= startTime
                  } else if (!i.starttime && i.endtime) {
                    return now <= endTime
                  }
                })
                ret = !!expireSwiper.length
                console.log('rotationChart filter end =======>')
              }
              return ret
            })
            console.log('this.dataConfig:')
            console.log(this.dataConfig)
            // 计算页面所有商品 sku 列表
            skuList = this.filterSku(this.dataConfig)
            if (skuList && skuList.length > 0) {
              // 根据sku查询商品实际信息
              let para = {
                skuList: skuList,
                shareId: this.getShareId,
                provinceId: this.location.provinceId,
                cityId: this.location.cityId,
                regionId: this.location.districtId,
                streetId: this.location.townshipId
              }
              personalApi.getCommonProductList(para).then(r => {
                console.log('r:', r)
                if (r.success) {
                  this.productActList = r.data?.productList || []
                  // 商品匹配实际价格 getInfoBySku
                  this.dataConfig.forEach(i => {
                    if (i.modelCode === 'bigPicture' || i.modelCode === 'bigRowTwoImage' || i.modelCode === 'bigRowThreeImage') {
                      // 三种商品组件
                      switch (i.modelCode) {
                        case 'bigPicture':
                          i.AddBigPicture.forEach(p => {
                            let finalPrice = this.getInfoBySku(p.sku).finalPrice || null
                            const url = this.getInfoBySku(p.sku).defaultImageUrl || null
                            p.img[0].price = finalPrice || p.img[0].price
                            p.img[0].url = url || p.img[0].url
                          })
                          break;
                        case 'bigRowTwoImage':
                          i.AddBigRowTwoImage.forEach(p => {
                            let finalPrice = this.getInfoBySku(p.sku).finalPrice || null
                            const url = this.getInfoBySku(p.sku).defaultImageUrl || null
                            p.img[0].price = finalPrice || p.img[0].price
                            p.img[0].url = url || p.img[0].url
                          })
                          break;
                        case 'bigRowThreeImage':
                          i.AddBigRowThreeImage.forEach(p => {
                            let finalPrice = this.getInfoBySku(p.sku).finalPrice || null
                            const url = this.getInfoBySku(p.sku).defaultImageUrl || null
                            p.img[0].price = finalPrice || p.img[0].price
                            p.img[0].url = url || p.img[0].url
                          })
                          break;
                      }
                    }
                  })
                }
                this.finished = true
                // 调公共价格接口在此开始元素曝光监听
                this.$nextTick(() => {
                  window.setTimeout(() => {
                    console.log('initdata  createIntersectionObserver====>0')
                    this.createIntersectionObserver()
                  }, 500)
                })
              })
            }
          } catch (e) {
            this.dataConfig = []
          }
          this.skeletonLoading = false
          if (!skuList.length) {
            // 不包含商品组件此处关闭 骨架屏
            this.finished = true
            // 不调公共价格接口在此开始元素曝光监听
            this.$nextTick(() => {
              window.setTimeout(() => {
                console.log('initdata  createIntersectionObserver====>1')
                this.createIntersectionObserver()
              }, 500)
            })
          }
          // this.$nextTick(() => {
          //   window.setTimeout(() => {
          //     console.log('initdata  createIntersectionObserver====>')
          //     this.createIntersectionObserver()
          //   }, 500)
          // })
        } else {
          if (res.success) {
            this.releaseStatus = res.result?.releaseStatus
            // if (res.result?.releaseStatus === 0) {
            // 配置未发布
            this.noData = true
            this.skeletonLoading = false
            // }
          }
          this.finished = true
        }
      })
    },
    /**
     * @description app页面监听器
    */
    addListener() {
      if (this.$util.isHaierApp()) {
        instance.initDeviceReady().then(() => {
          instance.addResumeEventListener(() => {
            console.log('app resume  createIntersectionObserver=======> 0')
            // 频道页浏览埋点
            let param = {
              page_id: this.channelId + '', // 页面id
              title: this.resultData?.pageName || 'null', // 页面标题
              url: document.URL + '', // 页面URL
              ex_page_url: document.referrer || 'null', // 上级页面URL/路径
              custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
            }
            this.$util.gioTrackWithVariable('mall_channel_view', param)
            // app 内重新激活页面时 重新监听元素曝光
            this.gioReportedList = []
            this.startTime = new Date().getTime()
            if (this.IntersectionObserver) {
              this.IntersectionObserver.disconnect()
            }
            this.$nextTick(() => {
              window.setTimeout(() => {
                this.createIntersectionObserver()
              }, 500)
            })
          }, 'onresume_key');
        });
      }
    },
    /**
     * @description 根据sku查询商品信息
    */
    getInfoBySku(sku) {
      let ret = {}
      this.productActList.forEach(i => {
        if (i.sku.toLocaleLowerCase() === sku.toLocaleLowerCase()) {
          ret = i
        }
      })
      return ret
    },
    /**
     * @description 判断当前环境
    */
    currentPlatform() {
      let ret = 'M站'
      if (this.$util.isSyBird()) {
        ret = '三翼鸟APP'
      } else if (this.$util.isSyBirdMP()) {
        ret = '三翼鸟小程序'
      } else if (this.$util.isScMpWechat()) {
        ret = '商城微信小程序'
      } else if (this.$util.isScMpPeWechat()) {
        ret = '商城私域微信小程序'
      } else if (this.$util.isUplus()) {
        ret = '智家APP'
      } else if (this.$util.isScMpByte()) {
        ret = '商城抖音私域小程序'
      } else if (this.$util.isScMpAli()) {
        ret = '支付宝小程序'
      }
      return ret
    },
    /**
     * @description 频道页元素曝光埋点
    */
    createIntersectionObserver () {
      const options = {
        root: document.querySelector('#app'),
        rootMargin: '12px 0px',
        threshold: [0.01]
      }
      if (this.IntersectionObserver) {
        this.IntersectionObserver.disconnect()
      }
      this.IntersectionObserver = new IntersectionObserver((entries) => {
        for (const ent of entries) {
          if (ent.isIntersecting) {
            let param = {
              site_number: ~~ent.target.dataset.index + 1 + '', // 元素顺序号
              remark: ent.target.dataset.remark || 'null', // 元素备注
              goto_url: ent.target.dataset.url?.trim() || 'null', // 跳转URL
              itemCode: ~~ent.target.dataset.mindex + 1 + '', // 模块顺序号
              content_type: ent.target.dataset.type || 'null', // 元素备注
              page_id: this.channelId + '', // 页面id
              title: this.resultData?.pageName || 'null', // 页面标题
              url: document.URL + '', // 页面URL
              custom_platform: this.currentPlatform() // 客户端 (“三翼鸟APP”、“三翼鸟小程序”、“商城微信小程序”、“商城私域微信小程序“、”智家APP”、“商城抖音私域小程序”、“支付宝小程序”、“M站”)
            }
            // 已经上报过的 曝光埋点不再重复上报
            if (!this.hasReported(param)) {
              this.$util.gioTrackWithVariable('mall_channel_view_item', param)
              this.gioReportedList.push({ site_number: param.site_number, itemCode: param.itemCode })
            }
            // console.log('observe', param)
          }
        }
      }, options)
      console.log('observe')
      for (const dom of document.querySelectorAll('.item')) {
        this.IntersectionObserver.observe(dom)
      }
    },
    /**
     * @description 埋点是否已上报
    */
    hasReported(gioParam) {
      let ret = false
      this.gioReportedList.forEach(i => {
        if (i.itemCode === gioParam.itemCode && i.site_number === gioParam.site_number) {
          ret = true
        }
      })
      return ret
    },
    /**
     * @description 从配置中过滤出商品组件的sku信息
    */
    filterSku(config) {
      let skuList = []
      config.forEach(i => {
        if (i.modelCode === 'bigPicture' || i.modelCode === 'bigRowTwoImage' || i.modelCode === 'bigRowThreeImage') {
          // 三种商品组件
          switch (i.modelCode) {
            case 'bigPicture':
              i.AddBigPicture.forEach(p => {
                if (skuList.indexOf(p.sku) === -1) {
                  skuList.unshift(p.sku)
                }
              })
              break;
            case 'bigRowTwoImage':
              i.AddBigRowTwoImage.forEach(p => {
                if (skuList.indexOf(p.sku) === -1) {
                  skuList.unshift(p.sku)
                }
              })
              break;
            case 'bigRowThreeImage':
              i.AddBigRowThreeImage.forEach(p => {
                if (skuList.indexOf(p.sku) === -1) {
