import store from '@/store'
import UplusApi from '@uplus/uplus-api';
const instance = new UplusApi();
instance.initDeviceReady()

/**
 * 小程序跳转工具
*/
export default {
  isIos() {
    return navigator.userAgent.indexOf('iPhone') > -1
  },
  isAndroid() {
    return navigator.userAgent.indexOf('Android') > -1
  },
  isHaierApp() {
    return navigator.userAgent.indexOf('App/Uplus') > -1 || navigator.userAgent.indexOf('App/SyBird') > -1
  },
  isIMaker() {
    return navigator.userAgent.indexOf('iMaker') > -1
  },
  isUplus() {
    return navigator.userAgent.indexOf('App/Uplus') > -1
  },
  isSyBird() {
    return navigator.userAgent.indexOf('App/SyBird') > -1
  },
  isSyBirdH5() {
    return (sessionStorage.getItem('open_source') === 'haier-sybird-h5' || sessionStorage.getItem('open_source') === 'haier-app-sybird-shared-h5')
  },
  isSyBirdSharedH5() {
    return sessionStorage.getItem('open_source') === 'haier-app-sybird-shared-h5'
  },
  isWeixin() {
    return /MicroMessenger/i.test(navigator.userAgent.toLowerCase())
  },
  isAlipay() {
    return /AlipayClient/i.test(navigator.userAgent.toLowerCase())
  },
  isIhaierApp() {
    return /Qing\/.*;(iOS|iPhone|Android).*/.test(navigator.userAgent)
  },
  isSgRn() {
    return navigator.userAgent.indexOf('ShunGuangRN') > -1
  },
  /**
   * 三翼鸟小程序, open_source 见《第三方对接文档》
   * @returns
   */
  isSyBirdMP() {
    return sessionStorage.getItem('open_source') === 'synmp' || sessionStorage.getItem('open_source') === 'haier-mp-sybird'
  },
  /**
   * @returns 商城微信小程序
   */
  isScMpWechat() {
    return sessionStorage.getItem('open_source') === 'sc-mp-wx'
  },
  /**
   * @returns 商城阿里小程序
   */
  isScMpAli() {
    return sessionStorage.getItem('open_source') === 'sc-mp-ali'
  },
  /**
   * @returns 商城字节小程序
   */
  isScMpByte() {
    return sessionStorage.getItem('open_source') === 'sc-mp-byte'
  },
  /**
   *
   * @returns 商城小程序 （含 微信 阿里 字节 三翼鸟小程序）
   */
  isScMp() {
    return (sessionStorage.getItem('open_source') && sessionStorage.getItem('open_source').startsWith('sc-mp')) || this.isSyBirdMP()
  },
  /**
   * 加载各端小程序依赖包，并加载uni依赖包
   */
  loadUniMPEnvDeps() {
    let mpenvsdkurl = ''
    if (this.isScMpWechat() || this.isSyBirdMP()) {
      mpenvsdkurl = '//res.wx.qq.com/open/js/jweixin-1.3.2.js'
    } else if (this.isScMpAli()) {
      mpenvsdkurl = '//appx/web-view.min.js'
    } else if (this.isScMpByte()) {
      mpenvsdkurl = '//s3.pstatp.com/toutiao/tmajssdk/jssdk-1.0.1.js'
    } else {
      return Promise.reject(new Error('env error, 环境不明确，检查open_source'))
    }
    return this.loadScript(mpenvsdkurl).then(() => {
      return this.loadScript('//js.cdn.aliyun.dcloud.net.cn/dev/uni-app/uni.webview.1.5.2.js').then(() => {
        console.log('uni:', window.uni)
      })
    })
  },
  loadScript(url, elemId) {
    return new Promise((resolve, reject) => {
      if (elemId && document.getElementById(elemId)) {
        resolve();
        return;
      }
      var script = document.createElement('script')
      script.type = 'text/javascript';
      if (elemId) script.id = elemId
      if (script.readyState) { // IE
        script.onreadystatechange = function() {
          if (script.readyState === 'loaded' || script.readyState === 'complete') {
            script.onreadystatechange = null;
            resolve();
          }
        };
      } else { // Others
        script.onload = function() {
          resolve();
        };
      }
      script.src = url;
      document.getElementsByTagName('head')[0].appendChild(script);
    })
  },
  /**
   * @description json 对象转query字符串
  */
  jsonToQuery(data) {
    let url = ''
    for (var k in data) {
      let value = data[k] !== undefined ? data[k] : ''
      url += '&' + k + '=' + encodeURIComponent(value)
    }
    return url ? '?' + url.substring(1) : ''
  },
  /**
   * @description 打开链接
  */
  openLink(page, param = {}, callBackPath) {
    if (this.isScMp()) {
      // 小程序环境
      if (!callBackPath) {
        callBackPath = window.location.href
      }
      console.log('query:', this.jsonToQuery(param))
      this.loadUniMPEnvDeps().then(() => {
        uni.postMessage({
          data: {
            type: ['path'],
            path: encodeURIComponent(callBackPath)
          }
        })
        uni.navigateTo({
          url: page + this.jsonToQuery(param)
        })
      }).catch(e => {
        // 拉取失败处理
        console.log(e)
      })
    } else {
      // this.$router.push({
      //   path: page,
      //   query: param
      // })
    }
  },
  /**
   * @description 公共链接跳转方法，图片跳转，banner跳转，标题链接跳转
   * @param linkType 链接类型 0:自定义链接 1:小程序路径
   * @param link 链接地址
   * @param showBar 是否展示 头部栏
   * @param needLogin 是否需要登录
  */
  handleLinkClick(options = {}) {
    if (options.linkType === 1) {
      if (options.needLogin) {
        if (store.state.isLogin) {
          // 跳转链接为小程序路径 ，拉起 uni 页面
          this.openLink(options.link)
        } else {
          if (this.isSyBirdMP()) {
            // 三翼鸟小程序 拉起三翼鸟小程序登陆页
            let synUrl = `/pages/publicPages/webView?url=${encodeURIComponent(options.link)}`
            this.openLink(synUrl)
          } else {
          // 跳转链接为小程序路径 ，拉起 uni 页面
            this.openLink(options.link)
          }
        }
      } else {
        // 跳转链接为小程序路径 ，拉起 uni 页面
        this.openLink(options.link)
      }
    } else {
      // 跳转链接为自定义链接
      if (!options.link || options.link.indexOf('http') === -1) return

      let targetUrl = options.link
      // // 是否展示容器头部栏
      // if (targetUrl.indexOf('?') !== -1) {
      //   targetUrl += `&show_title_bar=${!!options.showBar}`
      // } else {
      //   targetUrl += `?show_title_bar=${!!options.showBar}`
      // }
      console.log('needLogin======>', options.needLogin)
      if (options.needLogin) {
        console.log(store.state)
        console.log('login status:', store.state.isLogin)
        if (store.state.isLogin) { // 自定义链接无导航
          this.openNewWindow(targetUrl, true, true, !!options.showBar);
        } else {
          if (this.isSyBirdMP()) {
            // 三翼鸟小程序 拉起三翼鸟小程序登陆页
            // let synUrl = `/pages/publicPages/webView?url=${encodeURIComponent(targetUrl)}`
            let synUrl = `/pages/login/login?type=channel&url=${encodeURIComponent(targetUrl)}`
            console.log('synUrl====>', synUrl)
            this.openLink(synUrl)
          } else {
            store.dispatch('doLogin')
          }
        }
      } else {
        // window.location.href = targetUrl
        this.openNewWindow(targetUrl, true, true, !!options.showBar);
      }
    }
  },
  /**
   *
   * @param {*} url 跳转地址
   * @param {*} flag 表示是否为完整url
   * @param {*} skip 表示是否跳过 拼接 container_type=3&show_title_bar=true&hidesBottomBarWhenPushed=1
   * @param {*} showheader 是否带原生头
   */
  openNewWindow(url, flag, skip = false, showheader = true) {
    let targetUrl = url
    if (this.isHaierApp()) {
      if (!skip) {
        if (targetUrl.indexOf('#') !== -1) {
          let arr = targetUrl.split('#')
          let startStr = arr[0];
          let endStr = arr[1];
          if (arr[0].indexOf('?') !== -1) {
            startStr += `&container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          } else {
            startStr += `?container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          }
          targetUrl = startStr + '#' + endStr
        } else {
          if (targetUrl.indexOf('?') !== -1) {
            targetUrl += `&container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          } else {
            targetUrl += `?container_type=3&show_title_bar=${showheader}&hidesBottomBarWhenPushed=1`
          }
        }
      }
      instance.initDeviceReady().then(() => {
        // instance.vdnModule.vdnEngine.goToPage({ url: targetUrl });
        instance.upVdnModule.goToPage({ url: encodeURI(targetUrl) });
      });
    } else {
      window.location.href = targetUrl
    }
  },
}
