<template>
  <div class="rich-text-container item"
  :style="{'background-color': config.Backgrounds,
  'color': config.TextColor,
  'margin': `${config.TBPadding/50}rem  ${config.LRPadding/50}rem`,
  'font-size': (config.TextFontSize/50)+'rem',
  'line-height': ((config.TextLineHeight < config.TextFontSize?config.TextFontSize:config.TextLineHeight)/50)+'rem',
  'font-weight': config.TextType?'bold':'normal',
  'text-align': config.TextAlign}"
  :data-mIndex="index"
  :data-index="0"
  :data-remark="config.Remarks"
  :data-url="''"
  :data-type="config.modelCode"
  @click="handleClick"
  v-html="config.TextContent">
  </div>
</template>
<script>
import mixins from './common-mixins'

/**
 * @description 频道页 富文本组件
*/
export default {
  mixins: [mixins],
  props: {
  /**
     * 父组件传入配置
     */
    config: {
      type: Object,
      default: function () {
        return {}
      },
    },
    /**
     * @description 功能锁，为true不可点击跳转
    */
    lock: {
      type: Boolean,
      default: function () {
        return false
      },
    },
    /**
     * @description 模块顺序号
    */
    index: {
      type: Number,
      default: function () {
        return 0
      },
    },
  },
  data() {
    return {
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    handleClick() {
      if (this.lock) return
      let param = {
        site_number: '1',
        remark: this.config?.Remarks || 'null',
        goto_url: 'null',
        itemCode: this.index + 1 + '',
        content_type: this.config.modelCode,
        ...this.gioParams,
      }
      this.$util.gioTrackWithVariable('mall_channel_click_item', param) // 元素点击埋点
    },
  },
}
</script>

<style lang="scss" scoped>
.rich-text-container{
  background-color: #ffffff;
  color: #000000;
  font-weight: normal;
  text-align: left;
  font-size: .28rem;
  line-height: .4rem;
  word-break: break-all;
  // margin: .24rem;
  padding: .24rem;
  img{
    max-width: 100%;
  }

}
</style>
